import type { ClubId } from 'src/models/Club'
import type { Semester } from 'src/models/date'

export type Permission = 'USER' | 'ADMIN' | 'SUPERADMIN'
export type MemberStatus = 'ACTIVE' | 'PAUSED' | 'FIRED' | 'WITHDRAW' | ''
export type MemberLevel =
  | '정회원'
  | '준회원'
  | '교류회원'
  | '명예회원'
  | '임시회원'
  | '회원 분류 A'
  | '회원 분류 B'
  | '회원 분류 C'

export const isMemberLevel = (level: string): level is MemberLevel => {
  return ['정회원', '준회원', '교류회원', '명예회원', '임시회원', '회원 분류 A', '회원 분류 B', '회원 분류 C'].includes(
    level
  )
}

export type ClubMemberId = number
export interface ClubMember {
  id: ClubMemberId
  studentId: string
  name: string
  isAuthenticated: boolean
  email?: string
  phoneNumber?: string
  department?: string
  permission: Permission
  level: MemberLevel
  isPaused: boolean
  createdAt: string
  firedAt: string | null
  withdrawalAt: string | null
  deletedAt: string | null
  attendance: number
  totalAttendance: number
  fee?: boolean
  note: string
}

export interface ClubMemberForAuth {
  id: ClubId
  // 동아리 이름
  name: string
  permission: Permission
  isConnected: boolean
  year: number
  semester: Semester
  searchable: boolean
}
