import { useSnackbar } from 'notistack'
import { useEffect, useState } from 'react'
import { Helmet } from 'react-helmet-async'
import { useParams } from 'react-router'
import { useRecoilValue } from 'recoil'

import DeleteOutlineIcon from '@mui/icons-material/DeleteOutline'
import {
  Box,
  Card,
  Container,
  Divider,
  IconButton,
  MenuItem,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  TextField,
  Typography
} from '@mui/material'

import { MEMBER_VALUES } from 'src/data/MemberValues'
import { DEFAULT_DELETE_MESSAGE } from 'src/data/cofirmDialogueMessages'
import { ACCOUNT_MESSAGE } from 'src/data/messages'

import type { MemberLevel } from 'src/models/ClubMember'
import { isMemberLevel } from 'src/models/ClubMember'

import CancelSubmitBtns from 'src/components/CancelSubmitBtns'
import ConfirmDialog from 'src/components/ConfirmDialog'
import DateForm from 'src/components/DateForm'

import { CustomTableCell } from 'src/content/manage/Account/SemesterSummary'
import applyComma from 'src/content/manage/Account/applyComma'

import {
  type PostFeeAmountRequestData,
  type PutFeeAmountRequestData,
  useDeleteFeeAmount,
  useGetFeeAmounts,
  usePostFeeAmount,
  usePutFeeAmount
} from 'src/hooks/api/feeAmount'
import useSafeAuthUser from 'src/hooks/useSafeAuthUser'
import { dateState } from 'src/recoil/atom'
import { PureLightTheme } from 'src/theme/schemes/PureLightTheme'

const MemberLevelAndFeePage = () => {
  const date = useRecoilValue(dateState)
  const [confirmMode, setConfirmMode] = useState(false)
  const [deleteId, setDeleteId] = useState(0)
  const [editFeeId, setEditFeeId] = useState(0)
  const [editFeeLevel, setEditFeeLevel] = useState<MemberLevel | null>(null)
  const [editFeeAmount, setEditFeeAmount] = useState<number | null>(null)
  const [isEditMode, setIsEditMode] = useState(false)

  const { enqueueSnackbar } = useSnackbar()
  const { userInfo } = useSafeAuthUser()
  const { id } = useParams()
  const clubId = Number(id)

  const club = userInfo?.clubs.find(club => club.id === clubId)

  const { data: feeAmounts } = useGetFeeAmounts(clubId, date)

  const deleteFeeAmountMutation = useDeleteFeeAmount(clubId, deleteId)

  const putData: PutFeeAmountRequestData = { amount: editFeeAmount }
  const postData: PostFeeAmountRequestData = { level: editFeeLevel, amount: editFeeAmount }

  const putFeeAmountMutation = usePutFeeAmount(clubId, putData, editFeeId)
  const postFeeAmountMutation = usePostFeeAmount(clubId, date, postData)

  useEffect(() => {
    const fetchData = async () => {
      const foundItem = feeAmounts?.find(item => item.level === editFeeLevel)
      if (!foundItem) {
        setEditFeeId(0)
        setIsEditMode(false)
        setEditFeeAmount(null)
        return
      }
      setEditFeeId(foundItem.id)
      setEditFeeAmount(foundItem.amount)
      setIsEditMode(true)
    }
    fetchData()
  }, [editFeeLevel])

  const handleSubmitButtonClick: React.MouseEventHandler<HTMLButtonElement> = async () => {
    if (!editFeeLevel) {
      enqueueSnackbar(ACCOUNT_MESSAGE.FEE_INVALID_LEVEL, {
        variant: 'error'
      })
      return
    }
    if (!editFeeAmount || editFeeAmount < 0) {
      enqueueSnackbar(ACCOUNT_MESSAGE.INVALID_AMOUNT, {
        variant: 'error'
      })
      return
    }
    isEditMode ? putFeeAmountMutation.mutate() : postFeeAmountMutation.mutate()
  }

  const handleDeleteButtonClick: React.MouseEventHandler<HTMLButtonElement> = async () => {
    try {
      await deleteFeeAmountMutation.mutateAsync()

      setEditFeeAmount(null)
      setEditFeeLevel(null)
    } catch (err) {}
    setConfirmMode(false)
  }

  const handleIconButtonClick = (levelId: number) => {
    setDeleteId(levelId)
    setConfirmMode(true)
  }

  const handleChangeFeeLevel: React.ChangeEventHandler<HTMLInputElement> = e => {
    if (!isMemberLevel(e.target.value)) {
      enqueueSnackbar(ACCOUNT_MESSAGE.FEE_INVALID_LEVEL, {
        variant: 'error'
      })
      return
    }
    setEditFeeLevel(e.target.value)
  }

  const handleChangeFeeAmount: React.ChangeEventHandler<HTMLInputElement> = e => {
    setEditFeeAmount(parseInt(e.target.value))
  }

  return (
    <Container
      maxWidth={'lg'}
      sx={{
        height: '80vh',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center'
      }}
    >
      <Helmet>
        <title>회원등급 관리</title>
      </Helmet>
      <Box mt={4}>
        <DateForm club={club} />
        <Card sx={{ padding: { xs: 2, sm: 2, md: 4 } }}>
          <Typography variant={'h3'} align={'center'} mb={3} color={PureLightTheme.colors.primary.main}>
            회원등급 관리
          </Typography>

          <TableContainer>
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell width={'45%'}>회원등급</TableCell>
                  <TableCell width={'45%'}>회비</TableCell>
                  <TableCell width={'10%'}></TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {feeAmounts?.map((levels, key) => (
                  <TableRow key={key} hover>
                    <CustomTableCell cellValue={levels.level} />
                    <CustomTableCell cellValue={applyComma(levels.amount)} />
                    <TableCell>
                      <IconButton onClick={() => handleIconButtonClick(levels.id)}>
                        <DeleteOutlineIcon fontSize={'small'} color={'secondary'} />
                      </IconButton>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>

          <Box mt={2}>
            <Divider />
            <Box display={'flex'} flexDirection={'row'} mt={2}>
              <TextField
                id={'level'}
                name={'level'}
                label={'회원등급'}
                value={editFeeLevel ?? ''}
                onChange={handleChangeFeeLevel}
                select
                fullWidth
                style={{ marginRight: 10 }}
              >
                {MEMBER_VALUES.level.map(({ value, display }) => (
                  <MenuItem key={value} value={value}>
                    {display}
                  </MenuItem>
                ))}
              </TextField>
              <TextField
                label={'금액'}
                id={'amount'}
                value={editFeeAmount ?? ''}
                type={'number'}
                onChange={handleChangeFeeAmount}
                fullWidth
                style={{ marginLeft: 10 }}
              />
            </Box>
          </Box>

          <CancelSubmitBtns handleSubmit={handleSubmitButtonClick} submitBtnText={isEditMode ? '수정' : '등록'} />
        </Card>
      </Box>

      <ConfirmDialog
        open={confirmMode}
        handleClose={() => setConfirmMode(false)}
        handleSubmit={handleDeleteButtonClick}
        confirmMessage={DEFAULT_DELETE_MESSAGE}
      />
    </Container>
  )
}

export default MemberLevelAndFeePage
